body:not(.fl-builder-edit) .homepage-header-promo-slider {
  position: relative;
  top: 400px;
  border: 2px solid #DCE7ED;
  z-index: 100;
  border-radius: 27px;
  left: 200px;
}
body:not(.fl-builder-edit) .relative {
  position: relative;
}
body:not(.fl-builder-edit) .offset-slider-arrows .pp-image-carousel-wrapper .swiper-button-prev {
  left: -40px;
}
body:not(.fl-builder-edit) .offset-slider-arrows .pp-image-carousel-wrapper .swiper-button-next {
  right: -40px;
}
body:not(.fl-builder-edit) .events-layout {
  display: flex;
  gap: 20px;
}
body:not(.fl-builder-edit) .events-layout a {
  text-decoration: underline;
}
body:not(.fl-builder-edit) .events-layout h2 a {
  text-decoration: none;
}
body:not(.fl-builder-edit) .events-layout > div > h2 {
  font-weight: bold;
}
body:not(.fl-builder-edit) .events-layout .event-date {
  text-transform: uppercase;
  font-weight: bold;
}
body:not(.fl-builder-edit) .fl-post-text.events-text {
  padding-left: 0;
  padding-right: 0;
}
body:not(.fl-builder-edit) .event-grid .fl-post-column .fl-post-grid-post {
  border: none;
}